<template>
  <div class="page" id="record">
    <mt-header class="bar-nav" title="记录" >
      <mt-button slot="left" icon="back" v-back-link></mt-button>
    </mt-header>
    <ul class="rd-tab-title">
      <li @click="changeTab(1)">
        <span :class="{current: active == 'tab-con1'}">充值记录</span>
      </li>
      <li @click="changeTab(2)">
        <span :class="{current: active == 'tab-con2'}">提现记录</span>
      </li>
    </ul>
    <div class="record-title clearfix">
      <span v-show="active == 'tab-con1'" class="child text-left">充值状态/时间</span>
      <span v-show="active == 'tab-con2'"  class="child text-left">提现状态/时间</span>
      <span class="child text-right">交易金额(元)/流水号</span>
    </div>
    <div class="page-tab-container">
      <mt-tab-container class="page-tabbar-tab-container" v-model="active" swipeable>
        <mt-tab-container-item id="tab-con1">
          <div class="page-loadmore-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
            <mt-loadmore :bottom-method="loadBottom" @bottom-status-change="handleBottomChange" :bottom-all-loaded="allLoaded" ref="loadmore">
                <ul class="record-list">
                  <li v-for="item in list" class="clearfix">
                    <span class="text-left color-333 fz-16">{{item.statusStr}}</span>
                    <span class="text-right main-color fz-16">+{{item.amount | currency('',2)}}</span>
                    <span class="text-left color-999 fz-13">{{item.createTime | dateFormatFun(4)}}</span>
                    <span class="text-right color-999 fz-13">{{item.tradeNo}}</span>
                  </li>
                </ul>
                <div class="text-center no-data" v-show="noData">
                  <img src="../../assets/images/public/default/default_icon_no_record.png" alt="缺省图片">
                  <p>暂无记录</p>
                </div>
            </mt-loadmore>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="tab-con2">
          <div class="page-loadmore-wrapper" ref="" :style="{ height: wrapperHeight + 'px' }">
            <mt-loadmore :bottom-method="loadBottom" @bottom-status-change="handleBottomChange2" :bottom-all-loaded="allLoaded2" ref="loadmore2">
              <ul class="record-list">
                <li v-for="item in list2" class="clearfix">
                  <span class="text-left color-333 fz-16">{{item.statusStr}}</span>
                  <span class="text-right color-green fz-16">-{{item.amount | currency('',2)}}</span>
                  <span class="text-left color-999 fz-13">{{item.addTime | dateFormatFun(4)}}</span>
                  <span class="text-right color-999 fz-13">{{item.orderNo}}</span>
                </li>
              </ul>
              <div class="text-center no-data" v-show="noData">
                <img src="../../assets/images/public/default/default_icon_no_record.png" alt="缺省图片">
                <p>暂无记录</p>
              </div>
            </mt-loadmore>
          </div>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
  </div>
</template>
<script>
  import * as ajaxUrl from '../../ajax.config'
  export default {
    data() {
      return {
        active: this.$route.query.type,
        wrapperHeight: 0,
        list: [],
        list2: [],
        noData: false,
        allLoaded: false,
        allLoaded2: false,
        bottomStatus: '',
        bottomStatus2: '',
        page1: 1,
        page2: 1,
        totalPage1:1, //tab1默认总页数为1
        totalPage2:1, //tab2默认总页数为1
      }
    },
    methods: {
      changeTab(index=1){
        if(index == 1){
          this.active = 'tab-con1'
          this.list = []
        }else{
          this.active = 'tab-con2'
          this.list2 = []
        }
        this.projectList()
      },
      projectList(page=1){
        this.noData = false;
        let url
        if(this.active == 'tab-con1'){
          url = ajaxUrl.getRechargeList
        }else if(this.active == 'tab-con2'){
          url = ajaxUrl.getCashList
        }
        let urlParams = {
          'page.page': page,
          userId: this.$store.state.user.userId,
          __sid: this.$store.state.user.__sid,
        }
        this.$http.get(url, { params: urlParams }).then((res) => {
          if(res.data.resData.list.length == 0){this.noData = true;}// 数据为空判断
          if(page > res.data.resData.totalPage){
            this.$toast('无更多数据加载哦~')
            return ; // 请求页大于总页数就返回不执行
          }
          if(this.active == 'tab-con1'){
            this.list = this.list.concat(res.data.resData.list)
            this.totalPage1 = res.data.resData.pageSize
          }else if(this.active == 'tab-con2'){
            this.list2 = this.list2.concat(res.data.resData.list)
            this.totalPage2 = res.data.resData.pageSize
          }
        })
      },
      handleBottomChange(status) { this.bottomStatus = status;},
      handleBottomChange2(status) { this.bottomStatus2 = status;},
      loadBottom(id) {
        setTimeout(() => {
          if(this.active == 'tab-con1'){
            let page = ++this.page1
            this.projectList(page)
            this.$refs.loadmore.onBottomLoaded(id);
            if(page > this.totalPage1){
              this.allLoaded = true;// 若数据已全部获取完毕
            }
          }else if(this.active == 'tab-con2'){
            let page = ++this.page2
            this.projectList(page)
            this.$refs.loadmore2.onBottomLoaded(id);
            if(page > this.totalPage2){
              this.allLoaded2 = true;// 若数据已全部获取完毕
            }
          }
        }, 1500);
      }
    },
    created() {
      this.projectList()
      this.$nextTick(()=>{
        this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
      })
    },
  }
</script>
<style scoped>
  @import "../../assets/scss/var.scss";
  .rd-tab-title {
    width: 100%;
    height: .4rem;
    background: #fff;
  }
  .rd-tab-title li{
    width: 50%;
    line-height: .36rem;
    text-align: center;
    display: inline-block;
    float: left;
  }
  .current {
    color: $main-color;
    border-bottom: 2px solid $main-color;
  }
  .rd-tab-title li span {
    width: .6rem;
    display: block;
    margin: 0 auto;
  }
  .record-title{
    padding: 0 .15rem;
  }
  .record-title .child{
    display:inline-block;
    width: 49%;
    height: .35rem;
    line-height: .35rem;
    color: #666;
  }
  .text-left{
    text-align: left;
  }
  .text-right{
    text-align: right;
  }
  .record-list li{
    height: .7rem;
    padding: 0 .15rem;
    border-bottom: 1px solid #DDD;
    background: #fff;
  }
  .record-list li span{
    width: 50%;
    display: inline-block;
    float: left;
    line-height: 1;
    padding-top: .14rem;
  }
  /*加载更多组件样式*/
  @component-namespace page {
    @component loadmore {
      @descendent wrapper {
        overflow: scroll;
      }
      .mint-spinner {
        display: inline-block;
        vertical-align: middle;
      }
    }
  }
  @component mint-loadmore-bottom {
    span {
      display: inline-block;
      transition: .2s linear;
      vertical-align: middle;
      @when rotate {
        transform: rotate(180deg);
      }
    }
  }
</style>



